@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: "Roboto", "Kanit", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #292929;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::selection {
  color: white;
  background: #61d9fa;
}

#nav {
  position: fixed;
  z-index: 10;
  width: 100vw;
  height: 60px;

  display: flex;
  justify-content: center;
  align-items: center;
  background: #191919;
  color: white;
}

.nav-item {
  width: 100px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 3px;
  margin: 0 5px;

  font-size: 18px;
  transition: 0.2s;
  text-decoration: none;
}

.nav-item:hover {
  background: #292929;
}

.nav-item.active {
  color: #61d9fa;
}

a:-webkit-any-link {
  color: inherit;
}

a:-moz-any-link {
  color: inherit;
}

a:any-link {
  color: inherit;
}

.input {
  font-family: inherit;
  padding: 10px;
  border: none;
  border-radius: 0;
  background: white;

  min-width: 300px;
  font-size: 16px;
  transition: 0.2s;
}

.input:focus,
.input:hover {
  outline: none;
  background: #f7f7f7;
}

.input::-webkit-input-placeholder {
  color: #a4a4a4;
}

.input:-ms-input-placeholder {
  color: #a4a4a4;
}

.input::-ms-input-placeholder {
  color: #a4a4a4;
}

.input::placeholder {
  color: #a4a4a4;
}

.button {
  font-family: inherit;
  letter-spacing: 0.6px;
  width: 100px;
  height: 40px;
  color: white;
  background: #333333;
  border: none;
  border-radius: 0;

  cursor: pointer;
  transition: 0.2s;
}

.button:hover {
  background: #61d9fa;
}

.button:focus {
  outline: none;
}

.todo-list {
  width: 1200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.todo-title {
  font-size: 20px;
  text-align: left;
}

.todo-item {
  width: 600px;
  padding: 10px 20px;
  border: 1px solid #eeeeee;
  transition: 0.2s;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  position: relative;
}

.todo-item.done {
  color: #c4c4c4;
}

.todo-item.done:before {
  color: #c4c4c4;
  content: "";
  position: absolute;
  width: 80%;
  height: 2px;
  background: #61d9fa;
}

.todo-item:hover {
  border: 1px solid #c4c4c4;
}

.todo-item:hover > .del {
  opacity: 1;
}

.del {
  opacity: 0;
  transition: 0.2s;
  width: 40px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #777777;
}

.del:hover {
  background: #f7f7f7;
  color: red;
}

.home-page {
  width: 100%;
  min-height: 100vh;
  background-color: #292929;
  color: white;
  position: relative;
}

.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  opacity: 0.5;
  background: url(/react-todo/static/media/react.6a9eaaa4.svg) no-repeat center center;
}

.content {
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 56px;
  font-weight: 400;
}

.em {
  color: #61d9fa;
}

.createdby {
  color: #c4c4c4;
  font-size: 18px;
  letter-spacing: 1px;
}

.name {
  text-decoration: none;
}

.todo-page {
  width: 100%;
  min-height: 100vh;
  display: flex;
  padding-top: 160px;
  flex-direction: column;
  align-items: center;
}

